/**
 * plus.zip.compressImage,App
 */


let images = [];//
let max_width = 500;//,,,

/**
 * 
 */
async function compress(_images, _fun) {
	for (let i = 0; i < _images.length; i++) {//
		var compressd_image = await _compress(_images[i])
		images.push(compressd_image);
	}
	_fun && _fun(images);
	images = [];//images,
}

/**
 * QAQ~
 */
async function _compress(_image) {

	var last4chars = _image.slice(-4);
	if (plus.os.name == 'Android') { //plus.zip.compressImagejpeg/jpg/png
		if (!~last4chars.indexOf('jpg') && !~last4chars.indexOf('png') && !~last4chars.indexOf('jpeg')) {
			return _image;
		}
	}

	var image_info = await get_image_info(_image);
	if (image_info.width < max_width) { //800
		return _image;
	}

	return new Promise((resolve, reject) => {
		plus.zip.compressImage({
				src: _image,//
				dst: _image,//()
				overwrite: true,//,,,false,dst
				quality: 100,//1-100,,,
				width: max_width+'px',//800;heightauto,width
			},
			(res) => {
				resolve(res.target)
			},
			(e) => {
				reject(e);
			}
		)

	})


}

function get_image_info(_image) {
	return new Promise((resolve, reject) => {
		uni.getImageInfo({
			src: _image,
			success: res => {
				resolve(res);
			}
		})
	})
}

export default {
	compress
}
